








































































































.commpage {
    width: 100%;
    overflow: hidden;

    ::v-dedp .el-tabs__content {
        width: 100%;
    }

    ::v-deep .el-card__header {
        padding: 0.16rem 0.13rem 0.16rem 0.4rem;
    }

    ::v-deep .el-card__header {
        position: relative;
    }

    ::v-deep .el-card__header::after {
        content: "";
        position: absolute;
        background: #2e4af7;
        left: 0.24rem;
        top: 0.18rem;
        width: 6px;
        height: 0.2rem;
    }

    .clearfix {
        color: #333333;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 0.16rem;


        span {
            font-family: 'PFC';
            min-height: 21px;
        }
    }

    ::v-deep .el-card__body {
        padding: 0;
    }
}

.person-card {
    padding: 0.22rem 0.24rem 0.41rem 0.24rem;
    display: flex;

    .card-left {
        flex: 1;
    }

    .card-right {
        flex: 1;
    }

    .card-left,
    .card-right {
        display: flex;
        flex-direction: column;
    }

    .card-left>div:nth-child(1),
    .card-right>div:nth-child(1) {
        color: #909cb1;
        font-size: 14px;
    }

    .card-left>div:nth-child(2),
    .card-right>div:nth-child(2) {
        color: #333333;
        margin-top: 0.12rem;
        font-size: 16px;
        // font-weight: 500;
        font-family: 'PFC' !important;
    }
}

.person-tabs {
    margin-top: 0.24rem;
    width: 100%;
    background: #ffffff;
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
    border-radius: 6px;

    .tabs-head {
        width: 100%;
        padding: 0rem 0.24rem 0 0.24rem;
        background-color: #fff;
        box-sizing: border-box;
    }
}

.tabs-cont {
    padding: 0.05rem 0.4rem 0.45rem 0.4rem;
    height: 3.7rem;
    overflow-y: auto;
    width: 100%;
    box-sizing: border-box;

    .list-Item {
        display: flex;
        height: 0.82rem;
        padding-top: 0.22rem;
        border-bottom: 1px solid #eeeeee;

        .list-left {
            flex: 1;
            display: flex;
            height: 100%;
            color: #5e5e5e;
            font-size: 0.16rem;
            align-items: center;
        }

        .list-right {
            width: 0.65rem;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #2e4af7;
            cursor: pointer;
            //   div:nth-child(1) {
            //     color: #9b9b9b;
            //     font-size: 0.12rem;
            //   }
            //   .detail-btn {
            //     margin-top: 0.06rem;
            //     color: #2e4af7;
            //     font-size: 0.14rem;
            //  }
        }
    }
}

.person-info {
    display: flex;
}

.person-info>div:nth-child(1),
.person-info>div:nth-child(2) {
    color: #333333;
    font-size: 0.14rem;
}

.person-info>div:nth-child(2) {
    margin-left: 0.3rem;
    margin-right: 0.17rem;
}

.person-info>div:nth-child(2) span {
    color: #2e4af7;
}

.person-textarea {
    width: 100%;
}

.btn-box {
    margin-top: 0.22rem;
    display: flex;
    justify-content: flex-end;

    .submit {
        width: 0.65rem;
        height: 0.32rem;
        cursor: pointer;
        text-align: center;
        line-height: 0.32rem;
        color: #fff;
        background: #2e4af7;
        border-radius: 4px;
        margin-right: 8px;
    }

    .cancle {
        width: 0.65rem;
        height: 0.3rem;
        cursor: pointer;
        text-align: center;
        line-height: 0.3rem;
        color: #333;
        background: #fff;
        border: 1px solid #e3e5ec;
        border-radius: 4px;
    }
}

.smsTop {
    margin-top: 24px;
}
