



























































.bill-box {
    padding: 0.24rem 0 0.24rem 0;
    box-sizing: border-box;

    .bill-title {
        color: #000;
        font-size: 14px;
        margin-bottom: 20px;
    }

    .bill-info {
        width: 100%;
        height: 140px;
        padding-top: 9px;
        padding-left: 23px;
        background: rgba(46, 74, 247, 0.03);
        border-radius: 2px;
        border: 1px solid rgba(46, 74, 247, 0.31);
        box-sizing: border-box;
    }

    .bill-flex {
        display: flex;
    }

    .bill-item {
        flex: 1;
        margin-bottom: 14px;
    }

    .black {
        color: #000;
        border-bottom: 2px solid #000;
    }

    .bill-red {
        color: rgba(241, 23, 23, 0.65);
        border-bottom: 2px solid rgba(241, 23, 23, 0.65);
    }

}
